<template>
  <Card :bordered="false" dis-hover>
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-colon='true' label-position="top">
      <Row>

        <Col span="13" offset="4">
            <FormItem label="云区域名称" prop="name" v-if="formData.id" >
              <Input v-model="formValidate.name" maxlength="50" type="text" disabled placeholder="请输入规则名称..."></Input>
            </FormItem>
             <FormItem label="云区域名称" prop="name" v-else >
              <Input v-model="formValidate.name" maxlength="50" type="text" placeholder="请输入规则名称..."></Input>
            </FormItem>
      
            <Row :gutter="5">
              <Col span="12">
                 <FormItem label="云区域ID" prop="cloud_region_id">
                  <Input v-model="formValidate.cloud_region_id" maxlength="10" type="text" placeholder="云区域ID、代理ID..."></Input>
                </FormItem>
              </Col>
              <Col span="12">
                <FormItem label="代理地址" prop="proxy_ip" >
                  <Input v-model="formValidate.proxy_ip" maxlength="100" type="text" placeholder="连接代理节点IP地址或域名地址，一般为内网LB或者内网域名"></Input>
                </FormItem>
              </Col>
            </Row>

            <Row :gutter="5">
              <Col span="12">
                 <FormItem label="堡垒机组织ID" >
                  <Input v-model="formValidate.jms_org_id" maxlength="30" type="text" placeholder="堡垒机组织ID..."></Input>
                </FormItem>
              </Col>
              <Col span="12">
                <FormItem label="堡垒机账号模板ID" >
                  <Input v-model="formValidate.jms_account_template" maxlength="100" type="text" placeholder="堡垒机账号模板ID"></Input>
                </FormItem>
              </Col>
            </Row>
        </Col>
        
        <Col span="13" offset="4">
          <Row :gutter="2">
            <Col span="8">
              <FormItem label="" prop="ssh_user" >
                <Input v-model="formValidate.ssh_user" maxlength="25" type="text" placeholder="用户名..."><span slot="prepend">ssh</span></Input>
              </FormItem>
            </Col>
            <Col span="8">
              <FormItem label="" prop="ssh_ip" >
                <Input v-model="formValidate.ssh_ip" maxlength="100" type="text" placeholder="主机/ip..."><span slot="prepend">@</span></Input>
              </FormItem>
            </Col>
              <Col span="8">
               <FormItem label="" prop="ssh_port" >
                <Input v-model="formValidate.ssh_port" maxlength="10" type="text" placeholder="端口..."><span slot="prepend">-p</span></Input>
              </FormItem>
            </Col>
          </Row>
        </Col>
        <Col span="13" offset="4">
          <FormItem label="网域密钥">
           <Input v-model="formValidate.ssh_key"  type="textarea" maxlength="65500" :autosize="{minRows: 6,maxRows: 15}" placeholder="当前云区域网域私钥..." />
          </FormItem>
        </Col>

        <Col span="13" offset="4">
          <FormItem label="网域公钥">
           <Input v-model="formValidate.ssh_pub_key"  type="textarea" maxlength="65500" :autosize="{minRows: 3,maxRows: 15}" placeholder="当前云区域网域公钥..." />
          </FormItem>
        </Col>

        <Col span="13" offset="4">
          <FormItem label="备注信息">
           <Input v-model="formValidate.detail"  type="textarea" maxlength="65500" :autosize="{minRows: 2,maxRows: 5}" placeholder="备注信息..." />
          </FormItem>
        </Col>
        <Col span="19" offset="4">
        <FormItem>
          <Button type="primary" :loading="btnLoading" @click="handleSubmit('formValidate')">确定</Button>
            <Button @click="handleCancel" style="margin-left: 15px">返回</Button>
        </FormItem>

        </Col>
      </Row>
    </Form>

  </Card>
</template>
<script>
export default {
  components: {},
  name: 'cloudRegionEdit',
  props: {
    formData: {
      type: Object,
      default: {}
    },
  },
  data () {
    return {
      btnLoading: false,
      ruleValidate: {
        name: [{ required: true, message: '名称不能为空', trigger: 'blur' }],
        cloud_region_id: [{ required: true, message: '不能为空', trigger: 'blur' }],
        ssh_user: [{ required: true, message: '不能为空', trigger: 'blur' }],
        proxy_ip: [{ required: true, message: '不能为空', trigger: 'blur' }],
        detail: [{ type: 'string', max: 300, message: '备注也不能太长吧', trigger: 'blur' }],
      }
    }
  },
  methods: {
    handleCancel () {
      if (JSON.stringify(this.formValidate) != JSON.stringify(this.formData)) {
        this.$Modal.confirm({
          content: `<p>编辑还没有保存，确定要离开？</p>`,
          onOk: () => {
            this.$emit('edit-close')
          },
          onCancel: () => { this.$Message.info('回头是岸'); }
        });
      } else {
        this.$emit('edit-close')
      }
    },

    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.btnLoading = true
          this.formValidate.name = this.formValidate.name.trim()
          setTimeout(() => {
            this.$emit('save-data', this.formValidate)
            this.btnLoading = false
          }, 1000)
        } else {
          this.$Message.error('表单校验不通过!');
        }
      })
    },
 

  },
  watch: {
    formValidate (value) {
    },
  },
  computed: {
    formValidate: function () {
      return this.formData
    }
  },
  //
  mounted () {
  }
}
</script>

